body {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  color: #132633;
  background-color:rgb(155, 189, 189)
}

.title {
  padding: 30px;
  text-align: center;
  background-color:rgb(100, 184, 184);
  border: 2px solid #295470 ;
  /* border-radius: 10% */
}

h1 {
  font-size: 3em;
  /* margin: 52px 0 40px; */
  text-transform: uppercase;
  text-decoration: underline #132633;
}

h2 {
  font-size: 1.5em;
  margin: 52px 0 40px;
  text-transform: uppercase
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a {
  text-decoration: none;
}

.search-form {
  max-width: 460px;
  display: flex;
  margin: 0 auto 32px;
}

.search-form button {
  outline: none;
  background-color: #438bbd;
  border: 3px solid #132633;
  padding: 0px 15px;
  border-radius: 0 0.35em 0.35em 0;
  cursor: pointer;
}

input {
  font-size: 1em;
  width: 100%;
  background-color: #edeff0;
  padding: 10px 15px;
  border: 3px solid #132633;
  border-right: none;
  border-radius: 0.35em 0 0 0.35em;
  outline: none;
}

.main-nav li {
  width: 100%;
  margin-bottom: 1em;
}

.main-nav a {
  display: block;
  background: #438bbd;
  border-radius: 3px;
  padding: 5px;
  color: #fff;
}

.main-nav .active,
.main-nav a:hover,
.search-form button:hover {
  background-color: #275270;
}

.photo-container ul {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  -ms-align-items: flex-start;
  align-items: flex-start;
}

.photo-container ul li {
  width: 220px;
  height: 165px;
  overflow: hidden;
  margin-bottom: 15px;
  border: 2px solid #132633; 
  border-radius: 5%
}

.photo-container ul img {
  width: 100%;
  transform: scale(1.25);
  transition: transform 1.25s;
}

.photo-container li:hover img {
  transform: scale(1.2);
}

.photo-container .not-found {
  width: 100%;
}

@media only screen and (max-width: 767px)  {

  .photo-container li {
    margin: auto;
  }
}

@media only screen and (min-width: 768px)  {

  .container {
    max-width: 960px;
    margin: auto;
  }

  .main-nav ul {
    display: flex;
    justify-content: center;
  }
  
  .main-nav li {
    margin: 10px;
    width: 100px;
  }
}
